var layerbox = null;
var toastLayer = null;
function init() {
    layerbox = document.createElement('div');
    layerbox.setAttribute('style', 'display: none; background-color: rgba(0,0,0,.7); pointer-events: auto;' + 'position: fixed; left: 0; top: 0;width: 100%; height: 100%;z-index: 2019');
    layerbox.innerHTML = '<div id="m-layer-content" style="position: relative;width: 80%;padding: 10px;' + 'margin: 200px auto 0 auto;background-color: #fff;font-size: 14px;border-radius: 5px;' + 'text-align: center;box-sizing: content-box;"></div>';
    // append
    document.body.appendChild(layerbox);
    // addEventListener
    layerbox.addEventListener('click', function () {
        var ele = document.querySelector('#m-layer-content');
        if (ele) {
            ele.innerHTML = '';
        }
        layerbox.style.display = 'none';
    });
}
// if not in browsers, break
if (window && window.document) {
    init();
}
export function toast(content) {
    var _wx = {};
    try {
        _wx = wx;
    } catch (_a) {
        _wx = false;
    }
    if (_wx && _wx.canIUse && _wx.canIUse('showToast')) {
        _wx.showToast({
            title: content,
            icon: 'none'
        });
        return;
    }
    if (window && window.document) {
        var ele = window.document.querySelector('#m-layer-content');
        if (ele) {
            ele.innerHTML = content;
        }
        layerbox.style.display = 'block';
        return;
    }
}
export function showToast(msg, duration) {
    if (toastLayer != null) {
        return;
    }
    duration = isNaN(duration) ? 3000 : duration;
    toastLayer = document.createElement('div');
    toastLayer.innerHTML = msg;
    toastLayer.style.cssText = 'font-family:siyuan;max-width:80%;min-width: 180px;padding:0 30px;height: 45px;color: rgb(255, 255, 255);line-height: 45px;text-align: center;border-radius: 4px;position: fixed;top: 40%;left: 50%;transform: translate(-50%, -50%);z-index: 999999;background: rgba(0, 0, 0,.55);font-size: 16px;';
    document.body.appendChild(toastLayer);
    setTimeout(function () {
        var d = 0.5;
        toastLayer.style.webkitTransition = '-webkit-transform ' + d + 's ease-in, opacity ' + d + 's ease-in';
        toastLayer.style.opacity = '0';
        setTimeout(function () {
            document.body.removeChild(toastLayer);
            toastLayer = null;
        }, d * 1000);
    }, duration);
    return;
}